@use 'base/variables' as v;
@use 'base/mixins' as m;

.dashboard {
    text-align: center;
    margin-top: 3rem;

    @include m.tablet {
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
        gap: .5rem;
        height: 100vh;
        text-align: start;
    }


    .sidebar {
        border: 1px solid #2c2c2c;
        padding: 2rem;
        background: linear-gradient(to bottom, v.$grisClaro 50%, v.$grisOscuro 100%);
        height: 100%;
        border-radius: 10px;

        @include m.tablet {

            box-shadow: 25px -15px 10px -20px v.$grisOscuro;
        }

        h2 {
            color: v.$blanco;
        }

        .sidebar-nav {
            display: flex;
            flex-direction: column;
            gap: 2rem;

            a {
                color: v.$blanco;
                text-decoration: none;
                padding: 1rem;
                transition: all .3s ease;

                &.activo {
                    background-color: v.$gris;
                    border-radius: 20px;

                    span {
                        color: v.$blanco;
                        text-align: center;
                    }

                }

                span {
                    display: block;

                    @include m.tablet {
                        text-align: left;
                    }
                }

                &:hover {
                    background-color: darken(v.$blanco, 10);
                    border-radius: 20px;

                    span {
                        color: v.$negro;
                    }
                }

                @include m.tablet {

                    &:hover span {
                        text-align: center;
                    }

                }

            }
        }

    }


    .principal {
        margin: 2rem 0;
        padding: 0 1rem;


        @include m.tablet {
            flex-basis: 100%;
            margin: 0;
        }

        .contenido {
            margin-top: 1rem;
            text-align: center;




            .formulario {
                margin-bottom: 2rem;

                @include m.tablet {
                    position: relative;
                    min-height: 300px;


                    input[type="submit"] {
                        position: absolute;
                        right: 0;
                    }
                }


                input:not([type="submit"]) {
                    background-color: v.$blanco;
                    outline: 1px solid v.$gris;
                }


            }
        }


    }




}